export const generateAIContent = async (prompt) => {
  const API_ENDPOINT =
    "https://j9en8wwop2.execute-api.us-east-1.amazonaws.com/dev"; // Replace with your actual API endpoint

  try {
    console.log("Sending request to AI service:", prompt);
    const response = await fetch(API_ENDPOINT, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ prompt }),
    });

    console.log("Received response:", response.status, response.statusText);

    if (!response.ok) {
      const errorBody = await response.text();
      console.error("Error response body:", errorBody);
      throw new Error(
        `HTTP error! status: ${response.status}, body: ${errorBody}`
      );
    }

    const data = await response.json();
    console.log("Parsed response data:", data);

    if (typeof data.body === "string") {
      // Parse the body if it's a string
      const bodyData = JSON.parse(data.body);
      if (bodyData.result) {
        return bodyData.result;
      }
    }

    // If we couldn't find the result in the expected structure
    throw new Error("Unexpected response structure");
  } catch (error) {
    console.error("Error in generateAIContent:", error);
    throw error;
  }
};
