import { useState, useEffect } from "react";
import "./App.css";
import { generateAIContent } from "./aiservice";
import FormulaHelp from "./h";

const Spreadsheet = () => {
  const [showForm, setShowForm] = useState(true);
  const [rows, setRows] = useState(5);
  const [columns, setColumns] = useState(5);
  const [data, setData] = useState([]);
  const [displayData, setDisplayData] = useState([]);

  useEffect(() => {
    const updateDisplayData = async () => {
      const newDisplayData = await Promise.all(
        data.map(async (row) =>
          Promise.all(
            row.map(async (cell) => {
              if (cell.startsWith("=")) {
                return await evaluateFormula(cell);
              }
              return cell;
            })
          )
        )
      );
      setDisplayData(newDisplayData);
    };
    updateDisplayData();
  }, [data]);

  const handleCreateSpreadsheet = () => {
    setData(
      Array(rows)
        .fill()
        .map(() => Array(columns).fill(""))
    );
    setShowForm(false);
  };

  const handleBackToInput = () => {
    setShowForm(true);
  };

  const [loadingCells, setLoadingCells] = useState({});
  const renderCell = (rowIndex, colIndex) => {
    const cellValue = data[rowIndex][colIndex];
    const displayValue = displayData[rowIndex]?.[colIndex] ?? cellValue;
    const cellKey = `${rowIndex}-${colIndex}`;
    const isLoading = loadingCells[cellKey];

    const handleGenerate = async () => {
      if (cellValue.startsWith("=AI(")) {
        setLoadingCells((prev) => ({ ...prev, [cellKey]: true }));
        const aiPrompt = cellValue.slice(4, -1); // Remove "=AI(" and ")"
        try {
          const aiResult = await generateAIContent(aiPrompt);
          const newData = [...data];
          newData[rowIndex][colIndex] = `=${aiResult}`; // Prepend "=" to make it a formula
          setData(newData);
        } catch (error) {
          console.error("Error generating AI content:", error);
          const newData = [...data];
          newData[rowIndex][colIndex] = `#ERROR: ${
            error.message || "AI generation failed"
          }`;
          setData(newData);
        } finally {
          setLoadingCells((prev) => ({ ...prev, [cellKey]: false }));
        }
      }
    };

    return (
      <td>
        <input
          type="text"
          value={cellValue}
          onChange={(e) => handleChange(rowIndex, colIndex, e.target.value)}
          onBlur={() => handleBlur(rowIndex, colIndex)}
        />
        {cellValue.startsWith("=AI(") && (
          <button
            onClick={handleGenerate}
            className="generate-button"
            disabled={isLoading}
          >
            {isLoading ? "Generating..." : "Generate"}
          </button>
        )}
        {cellValue.startsWith("=") && !cellValue.startsWith("=AI(") && (
          <div className="formula-result">{displayValue}</div>
        )}
      </td>
    );
  };

  const cellToIndex = (cellRef) => {
    if (/^[A-Z]$/.test(cellRef)) {
      // Handle column-only reference
      return [null, cellRef.charCodeAt(0) - 65];
    }
    const col = cellRef.charCodeAt(0) - 65;
    const row = parseInt(cellRef.slice(1)) - 1;
    return [row, col];
  };

  const handleChange = (row, col, value) => {
    const newData = [...data];
    newData[row][col] = value;
    setData(newData);
  };

  const handleBlur = async (row, col) => {
    const cellVal = data[row][col];
    if (cellVal.startsWith("=") && !cellVal.startsWith("=AI(")) {
      try {
        const result = await evaluateFormula(cellVal);
        const newDisplayData = [...displayData];
        newDisplayData[row][col] = result;
        setDisplayData(newDisplayData);
      } catch (error) {
        console.error("Error evaluating formula:", error);
      }
    }
  };

  const evaluateCellReference = async (cellRef, visited) => {
    const [row, col] = cellToIndex(cellRef);
    const key = `${row},${col}`;
    if (visited.has(key)) {
      return "#CIRCULAR";
    }
    visited.add(key);
    let value;
    try {
      value = await evaluateFormula(data[row][col], new Set(visited));
    } catch (error) {
      if (error.message === "Circular reference detected") {
        value = "#CIRCULAR";
      } else {
        value = "#ERROR";
      }
    }
    visited.delete(key);
    return value;
  };

  const evaluateColumnReference = (colRef) => {
    const col = colRef.charCodeAt(0) - 65;
    return data.map((row) => row[col]).filter((cell) => cell !== "");
  };

  const evaluateRange = async (start, end, visited) => {
    const [startRow, startCol] = cellToIndex(start);
    const [endRow, endCol] = cellToIndex(end);

    const range = [];
    const actualEndRow = endRow === null ? data.length - 1 : endRow;
    const actualStartRow = startRow === null ? 0 : startRow;

    for (let i = actualStartRow; i <= actualEndRow; i++) {
      for (let j = startCol; j <= endCol; j++) {
        const cellRef = `${String.fromCharCode(65 + j)}${i + 1}`;
        range.push(await evaluateCellReference(cellRef, visited));
      }
    }
    return range;
  };

  const evaluateFormula = async (formula, visited = new Set()) => {
    if (!formula.startsWith("=")) {
      return isNaN(formula) ? formula : Number(formula);
    }

    if (formula.startsWith("=AI(")) {
      return "Click 'Generate' to evaluate AI function";
    }

    const functionMatch = formula.match(/(\w+)\((.*)\)/);
    if (functionMatch) {
      const [, functionName, argString] = functionMatch;
      const args = await Promise.all(
        argString.split(",").map(async (arg) => {
          const trimmed = arg.trim();
          if (trimmed.includes(":")) {
            var [start, end] = trimmed.split(":");

            if (!isNaN(start) && !isNaN(end)) {
              const startNum = parseInt(start);
              const endNum = parseInt(end);
              return Array.from(
                { length: endNum - startNum + 1 },
                (_, i) => startNum + i
              );
            }

            if (/\d/.test(start) === false) {
              start = start + "1";
            }
            if (/\d/.test(end) === false) {
              end = end + data.length.toString();
            }
            const [startRow, startCol] = cellToIndex(start);
            const [endRow, endCol] = cellToIndex(end);
            const range = [];
            for (let i = startRow; i <= endRow; i++) {
              for (let j = startCol; j <= endCol; j++) {
                range.push(
                  await evaluateCellReference(
                    `${String.fromCharCode(65 + j)}${i + 1}`,
                    visited
                  )
                );
              }
            }
            return range;
          } else if (/^[A-Z]$/.test(trimmed)) {
            return evaluateRange(
              trimmed + "1",
              trimmed + toString(data.length),
              visited
            );
          } else if (/^[A-Z][1-9][0-9]?$/.test(trimmed)) {
            return await evaluateCellReference(trimmed, visited);
          } else {
            return isNaN(trimmed) ? trimmed : Number(trimmed);
          }
        })
      );

      const flatArgs = args.flat();

      if (flatArgs.includes("#CIRCULAR")) {
        return "#CIRCULAR";
      }

      if (spreadsheetfun[functionName]) {
        return await spreadsheetfun[functionName](flatArgs);
      }
    }

    // For non-function formulas ( e.g., =A1+B1)
    try {
      const expression = await Promise.all(
        formula
          .slice(1)
          .split(/([+\-*/])/)
          .map(async (part) => {
            if (/^[A-Z][1-9][0-9]?$/.test(part)) {
              const value = await evaluateCellReference(part, visited);
              if (value === "#CIRCULAR") {
                throw new Error("Circular reference detected");
              }
              return isNaN(value) ? `"${value}"` : value;
            }
            return part;
          })
      );
      return eval(expression.join(""));
    } catch (error) {
      if (error.message === "Circular reference detected") {
        return "#CIRCULAR";
      }
      return "#ERROR";
    }
  };

  const spreadsheetfun = {
    SUM: (args) => {
      if (args.includes("#CIRCULAR")) return "#CIRCULAR";
      return args.reduce(
        (sum, value) => sum + (isNaN(value) ? 0 : Number(value)),
        0
      );
    },
    AVERAGE: (args) => {
      if (args.includes("#CIRCULAR")) return "#CIRCULAR";
      const validNumbers = args.filter((v) => !isNaN(v)).map(Number);
      return validNumbers.length
        ? validNumbers.reduce((a, b) => a + b) / validNumbers.length
        : 0;
    },
    MAX: (args) => {
      if (args.includes("#CIRCULAR")) return "#CIRCULAR";
      const validNumbers = args.filter((v) => !isNaN(v)).map(Number);
      return validNumbers.length ? Math.max(...validNumbers) : 0;
    },
    MIN: (args) => {
      if (args.includes("#CIRCULAR")) return "#CIRCULAR";
      const validNumbers = args.filter((v) => !isNaN(v)).map(Number);
      return validNumbers.length ? Math.min(...validNumbers) : 0;
    },
    COUNT: (args) => args.length,
    POW: (args) => {
      if (args.includes("#CIRCULAR")) return "#CIRCULAR";
      const [base, exp] = args;
      // Convert arguments to numbers
      const baseNum = Number(base);
      const expNum = Number(exp);
      // Check if the conversion was successful
      if (isNaN(baseNum) || isNaN(expNum)) {
        return "#ERROR";
      }
      try {
        // Calculate the power
        const result = Math.pow(baseNum, expNum);
        // Check for special cases
        if (!isFinite(result)) {
          return "#OVERFLOW";
        }
        return result;
      } catch (error) {
        console.error("Error in POW function:", error);
        return "#ERROR";
      }
    },
  };
  if (showForm) {
    return (
      <div className="main">
        <header>
          <h1>Welcome to Simple Spreadsheet</h1>
        </header>

        <div className="input">
          <label htmlFor="rows">Number of Rows: </label>
          <input
            type="number"
            id="rows"
            value={rows}
            onChange={(e) =>
              setRows(
                e.target.value === ""
                  ? ""
                  : Math.max(1, parseInt(e.target.value) || 0)
              )
            }
            min="1"
          />
        </div>
        <div className="input">
          <label htmlFor="columns">Number of Columns</label>
          <input
            type="number"
            id="rows"
            value={columns}
            onChange={(e) =>
              setColumns(
                e.target.value === ""
                  ? ""
                  : Math.max(1, parseInt(e.target.value) || 0)
              )
            }
            min="1"
          />
        </div>
        <button onClick={handleCreateSpreadsheet}>Create Speadsheet</button>
      </div>
    );
  }
  if (!showForm) {
    return (
      <div>
        <header>
          <h1>Simple Spreadsheet</h1>
          <FormulaHelp />
        </header>
        <table>
          <thead>
            <tr>
              <th></th>
              {data[0].map((_, index) => (
                <th key={index}>{String.fromCharCode(65 + index)}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {data.map((row, rowIndex) => (
              <tr key={rowIndex}>
                <th>{rowIndex + 1}</th>
                {row.map((_, colIndex) => (
                  <td key={colIndex}>{renderCell(rowIndex, colIndex)}</td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
        <button className="back-button" onClick={handleBackToInput}>
          Back to input
        </button>
      </div>
    );
  }
};

export default Spreadsheet;
