import React, { useState } from "react";

const FormulaHelp = () => {
  const [isOpen, setIsOpen] = useState(false);

  const formulas = [
    {
      name: "SUM",
      description: "Adds up all the numbers in the selected range or list.",
      usage: "=SUM(A1:A5) or =SUM(1, 2, 3, 4, 5)",
    },

    {
      name: "AI",
      description:
        "Uses natural language processing to interpret your request and perform the desired operation.",
      usage:
        '=AI("Add up all numbers in column A") or =AI("Calculate the average of cells B1 to B10")',
    },
    {
      name: "AVERAGE",
      description: "Calculates the arithmetic mean of the numbers.",
      usage: "=AVERAGE(B1:B10) or =AVERAGE(10, 20, 30)",
    },
    {
      name: "MAX",
      description: "Returns the largest value in the selected range or list.",
      usage: "=MAX(C1:C20) or =MAX(5, 10, 15, 20)",
    },
    {
      name: "MIN",
      description: "Returns the smallest value in the selected range or list.",
      usage: "=MIN(D1:D15) or =MIN(100, 200, 50, 300)",
    },
    {
      name: "COUNT",
      description: "Counts the number of cells that contain numbers.",
      usage: '=COUNT(E1:E50) or =COUNT(1, "text", 2, 3)',
    },
    {
      name: "PRODUCT",
      description: "Multiplies all the numbers given as arguments.",
      usage: "=PRODUCT(F1:F3) or =PRODUCT(2, 3, 4)",
    },
  ];

  return (
    <div style={{ position: "relative" }}>
      <button
        onClick={() => setIsOpen(!isOpen)}
        style={{
          padding: "8px",
          width: "40px",
          background: "#2980b9",
          border: "none",
          borderRadius: "15px",
          cursor: "pointer",
          fontSize: "24px",
        }}
        aria-label="Help"
      >
        ?
      </button>
      {isOpen && (
        <div
          style={{
            position: "absolute",
            right: 0,
            marginTop: "8px",
            width: "300px",
            backgroundColor: "white",
            borderRadius: "8px",
            boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
            zIndex: 10,
            maxHeight: "400px",
            overflowY: "auto",
            padding: "16px",
          }}
        >
          <h3
            style={{
              fontSize: "18px",
              fontWeight: "bold",
              marginBottom: "12px",
            }}
          >
            Available Formulas
          </h3>
          <ul style={{ listStyleType: "none", padding: 0 }}>
            {formulas.map((formula, index) => (
              <li
                key={index}
                style={{
                  marginBottom: "16px",
                  borderBottom: "1px solid #eee",
                  paddingBottom: "8px",
                }}
              >
                <strong style={{ color: "#2c3e50" }}>{formula.name}</strong>
                <p style={{ fontSize: "14px", margin: "4px 0" }}>
                  {formula.description}
                </p>
                <p
                  style={{
                    fontSize: "14px",
                    fontFamily: "monospace",
                    backgroundColor: "#f0f0f0",
                    padding: "4px",
                    borderRadius: "4px",
                  }}
                >
                  Usage: {formula.usage}
                </p>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default FormulaHelp;
